@use '../../scss/' as *;
.seach__container{
    display: flex;
    align-items: center;
    padding: 8px 0;
    &__img{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        /* margin-right: 8px; */
        margin: 0px 8px;
        object-fit: fill;
        object-position: center;
        flex-shrink: 0;
}
    &__infor{
        font-weight: 600;
    }
}

